<template lang="pug">

    .container
        div(v-if="getAllAccounts.money_market.length > 0")

            .row.mb-2.mt-4
                .col-md-6
                    div Money Markey Accounts
            hr
            br 
            .row.mt-1
                .col-md-12
                    ejs-grid(
                        :data-source="moneyMarket"
                        :allowPaging="true"
                        :pageSettings="pageSettings"
                        :editSettings="editOptions"
                        :toolbar="toolbarOptions"
                    )
                        e-columns
                            e-column(
                                field="Account_Number" headerText="Account Number"
                                :isPrimaryKey="true"
                            )
                            e-column(
                                field="Account_Type" headerText="Account Type"
                                :isPrimaryKey="true"

                            )
                            e-column(
                                field="Account_Name" headerText="Account Name"
                                :isPrimaryKey="true"
                            )
                            e-column(
                                field="Account_Purpose" headerText="Account Purpose"
                                :value="moneyMarket.account_purpose"
                            )
                            e-column(
                                field="Action" headerText="Action"
                            )
           

</template>
<script>
import {mapGetters} from 'vuex'
import { Page, Edit, Toolbar } from '@syncfusion/ej2-vue-grids'
export default {
    name:'Accounts',
    data: () => ({
        pageSettings: {pageSize:3},
        toolbarOptions: ["Edit", "Update", "Cancel"],
        editOptions: {allowEditing:true, mode:'Normal'},
        purpose: '',
    }),
    methods:{
        cleanString(data){
            return data.replace(/[|&;$%@"<>()+,-]/g, "");
        },
        update(e){
            console.log(e.text)
        }
    },

    computed:{
        ...mapGetters(['getAllAccounts']),

        moneyMarket(){
            const data = []
            this.getAllAccounts.money_market.forEach(row => {
                data.push({
                        Account_Type:'money_market',
                        Account_Number: row.MEMBER_NO,
                        Account_Name: this.cleanString(row.ALLNAMES),
                        Account_Purpose: 'savings',
                        Action:  true
                        
                    })
            })
            return data
            
        },
        pension(){
            const data = []
            this.getAllAccounts.pension.forEach(row => {
                data.push({
                        account_type:'pension',
                        account_number: row.MEMBER_NO,
                        account_name: row.ALLNAMES,
                        account_purpose: 'savings',
                        action: true
                        
                    })
            })
            
            return data
        },


    },
    provide:{
        grid: [Page, Edit, Toolbar]
    },
    beforeCreate(){
        this.$store.dispatch('retrieveAccounts')
    }

}
</script>

<style>

@import url(https://cdn.syncfusion.com/ej2/bootstrap.css);

</style>